import { glob } from 'glob'
import { readFile, writeFile, remove } from 'fs-extra'
import { rollup, Plugin } from 'rollup'
import url from '@rollup/plugin-url'
import copy from 'rollup-plugin-copy'
import json from '@rollup/plugin-json'
import babel from '@rollup/plugin-babel'
import vuePlugin from 'rollup-plugin-vue'
import styles from 'rollup-plugin-styles'
import esbuild from 'rollup-plugin-esbuild'
import postcss from 'rollup-plugin-postcss'
import replace from '@rollup/plugin-replace'
import { terser } from 'rollup-plugin-terser'
import commonjs from '@rollup/plugin-commonjs'
import nodeResolve from '@rollup/plugin-node-resolve'
import ignoreImport from 'rollup-plugin-ignore-import'
import multiInput from 'rollup-plugin-multi-input'
import staticImport from 'rollup-plugin-static-import'

/**
 * ÊûÑÂª∫ÈÖçÁΩÆÊé•Âè£
 */
export interface BuildConfig {
  /** È°πÁõÆÂêçÁß∞ */
  name: string
  /** ÁâàÊú¨Âè∑ */
  version: string
  /** ‰ΩúËÄÖ */
  author?: string
  /** ËÆ∏ÂèØËØÅ */
  license?: string
  /** ÂÖ•Âè£Êñá‰ª∂ */
  input?: string | string[]
  /** ËæìÂá∫ÁõÆÂΩï */
  outDir?: string
  /** Â§ñÈÉ®‰æùËµñ */
  external?: string[]
  /** ÂÖ®Â±ÄÂèòÈáèÊò†Â∞Ñ */
  globals?: Record<string, string>
  /** ÊòØÂê¶‰∏∫ Vue ÁªÑ‰ª∂Â∫ì */
  isVueLibrary?: boolean
  /** ÁªÑ‰ª∂Ê†πÁõÆÂΩï */
  componentsRoot?: string
}

/**
 * CSS ÊûÑÂª∫Á≠ñÁï•
 */
export type CSSBuildType = 'single' | 'multi' | 'source' | 'ignore'

/**
 * ÈªòËÆ§Êâ©Â±ïÂêç
 */
const DEFAULT_EXTENSIONS = ['.js', '.jsx', '.es6', '.es', '.mjs', '.cjs']

/**
 * Ëé∑ÂèñÊèí‰ª∂ÈÖçÁΩÆ
 */
const getPlugins = ({
  cssBuildType,
  env,
  isProd,
  config
}: {
  cssBuildType?: CSSBuildType
  env?: string
  isProd?: boolean
  config: BuildConfig
} = {} as any) => {
  const plugins = [
    nodeResolve({
      extensions: ['.mjs', '.js', '.json', '.node', '.ts', '.tsx', '.vue'],
    }) as unknown as Plugin,
    commonjs(),
    esbuild({
      target: 'esnext',
      minify: false,
      jsx: 'preserve',
    }),
    json(),
    url(),
    replace({
      preventAssignment: true,
      values: {
        PKG_VERSION: JSON.stringify(config.version),
      },
    }),
  ]

  // Â¶ÇÊûúÊòØ Vue ÁªÑ‰ª∂Â∫ìÔºåÊ∑ªÂä† Vue Êèí‰ª∂
  if (config.isVueLibrary) {
    plugins.unshift(vuePlugin())

    // Babel ËΩ¨Êç¢ÈÖçÁΩÆ
    plugins.push(babel({
      babelHelpers: 'runtime',
      extensions: [...DEFAULT_EXTENSIONS, '.vue', '.ts', '.tsx'],
      exclude: 'node_modules/**'
    }))
  }

  // CSS Â§ÑÁêÜÁ≠ñÁï•
  if (cssBuildType === 'single') {
    // CSS ÊâìÂåÖÂà∞‰∏Ä‰∏™Êñá‰ª∂‰∏≠
    plugins.push(
      postcss({
        extract: `${isProd ? `${config.name}.min` : config.name}.css`,
        minimize: isProd,
        sourceMap: true,
        extensions: ['.sass', '.scss', '.css', '.less'],
      }),
    )
  } else if (cssBuildType === 'multi') {
    // CSS ÂàÜÂà´ÊâìÂåÖÂà∞ÂêÑËá™ÁöÑ style ‰∏ã
    const componentsRoot = config.componentsRoot || 'src'

    plugins.push(
      staticImport({
        baseDir: componentsRoot,
        include: [`${componentsRoot}/**/style/css.mjs`],
      }),
      ignoreImport({
        include: [`${componentsRoot}/**/style/*`],
        body: 'import "./style/css.mjs";',
      }),
      copy({
        targets: [
          {
            src: `${componentsRoot}/**/style/css.js`,
            dest: `${config.outDir || '.'}/es`,
            rename: (name, extension, fullPath) =>
              `${fullPath.replace(componentsRoot + '/', '').slice(0, -6)}${name}.mjs`,
          },
        ],
        verbose: true,
      }),
    )
  } else if (cssBuildType === 'ignore') {
    // ÂÆåÂÖ®ÂøΩÁï•Ê†∑Âºè
    plugins.push(
      ignoreImport({
        include: [`${config.componentsRoot || 'src'}/**/style/index.js`],
      }),
    )
  }
  // cssBuildType === 'source' Êó∂‰∏çÊ∑ªÂä†Ê†∑ÂºèÂ§ÑÁêÜÊèí‰ª∂Ôºå‰øùÊåÅÂºïÁî®

  if (env) {
    plugins.push(
      replace({
        preventAssignment: true,
        values: {
          'process.env.NODE_ENV': JSON.stringify(env),
        },
      }),
    )
  }

  if (isProd) {
    plugins.push(
      terser({
        output: {
          ascii_only: true,
        },
      }),
    )
  }

  return plugins
}

/**
 * ÁîüÊàê banner
 */
const createBanner = (config: BuildConfig) => {
  return `/**
 * ${config.name} v${config.version}
 * (c) ${new Date().getFullYear()} ${config.author || 'LDesign Team'}
 * @license ${config.license || 'MIT'}
 */
`
}

/**
 * ÊûÑÂª∫ ES Ê®°ÂùóÔºàÊîØÊåÅ tree-shakingÔºâ
 */
export const buildEs = async (config: BuildConfig) => {
  console.log('üî® ÊûÑÂª∫ ES Ê®°Âùó...')

  const componentsRoot = config.componentsRoot || 'src'
  const inputList = [
    `${componentsRoot}/**/*.ts`,
    `${componentsRoot}/**/*.tsx`,
    `!${componentsRoot}/**/demos`,
    `!${componentsRoot}/**/*.d.ts`,
    `!${componentsRoot}/**/type.ts`,
    `!${componentsRoot}/**/types.ts`,
    `!${componentsRoot}/**/__tests__`,
  ]

  // ÂÖàÊûÑÂª∫ CSS
  if (config.isVueLibrary) {
    const cssBundle = await rollup({
      input: [`${componentsRoot}/**/style/index.js`],
      plugins: [multiInput({ relative: componentsRoot }), styles({ mode: 'extract' }), nodeResolve()],
    })

    await cssBundle.write({
      banner: createBanner(config),
      dir: `${config.outDir || '.'}/es/`,
      assetFileNames: '[name].css',
    })
  }

  // ÊûÑÂª∫ÁªÑ‰ª∂
  const external = [...(config.external || []), 'vue']
  const bundle = await rollup({
    input: [...inputList, `!${componentsRoot}/index-lib.ts`],
    treeshake: false, // ‰∏∫‰∫Ü‰øùÁïô style/css.js
    external,
    plugins: [multiInput({ relative: componentsRoot }), ...getPlugins({ cssBuildType: 'multi', config })],
  })

  await bundle.write({
    banner: createBanner(config),
    dir: `${config.outDir || '.'}/es/`,
    format: 'esm',
    sourcemap: true,
    entryFileNames: '[name].mjs',
    chunkFileNames: '_chunks/dep-[hash].mjs',
  })

  // Ê∏ÖÁêÜ‰∏çÈúÄË¶ÅÁöÑÊñá‰ª∂
  const files = await glob(`${config.outDir || '.'}/es/**/style/index.js`)
  await Promise.all(files.map(filePath => remove(filePath)))

  console.log('‚úÖ ES Ê®°ÂùóÊûÑÂª∫ÂÆåÊàê')
}

/**
 * ÊûÑÂª∫ ESM Ê®°ÂùóÔºà‰øùÁïôÊ∫êÁ†ÅÊ†∑ÂºèÂºïÁî®Ôºâ
 */
export const buildEsm = async (config: BuildConfig) => {
  console.log('üî® ÊûÑÂª∫ ESM Ê®°Âùó...')

  const componentsRoot = config.componentsRoot || 'src'
  const inputList = [
    `${componentsRoot}/**/*.ts`,
    `${componentsRoot}/**/*.tsx`,
    `!${componentsRoot}/**/demos`,
    `!${componentsRoot}/**/*.d.ts`,
    `!${componentsRoot}/**/type.ts`,
    `!${componentsRoot}/**/types.ts`,
    `!${componentsRoot}/**/__tests__`,
  ]

  const external = [...(config.external || []), 'vue', /@ldesign\/common-style/]
  const bundle = await rollup({
    input: [...inputList, `!${componentsRoot}/index-lib.ts`],
    external,
    plugins: [
      multiInput({ relative: componentsRoot }),
      // Â§çÂà∂ÈÄöÁî®Ê†∑Âºè
      copy({
        targets: [
          {
            src: 'src/styles/**/*.less',
            dest: `${config.outDir || '.'}/esm/common`,
            rename: (_, __, fullPath) => `${fullPath.replace('src/', '')}`,
          },
        ],
        verbose: true,
      }),
      ...getPlugins({ cssBuildType: 'source', config }),
    ],
  })

  await bundle.write({
    banner: createBanner(config),
    dir: `${config.outDir || '.'}/esm/`,
    format: 'esm',
    sourcemap: true,
    chunkFileNames: '_chunks/dep-[hash].js',
  })

  // ÊõøÊç¢Ê†∑ÂºèÂØºÂÖ•Ë∑ØÂæÑ
  const files = await glob(`${config.outDir || '.'}/esm/**/*.*`)
  const rewritePromises = files.map(async (filePath) => {
    const content = await readFile(filePath, 'utf8')
    const newContent = content.replace(/@ldesign\/common-style/g, `${config.name}/esm/common/style`)
    if (content !== newContent) {
      await writeFile(filePath, newContent, 'utf8')
    }
  })
  await Promise.all(rewritePromises)

  console.log('‚úÖ ESM Ê®°ÂùóÊûÑÂª∫ÂÆåÊàê')
}

