/**
 * ÊûÑÂª∫ CJS Ê†ºÂºè
 */
export const buildCjs = async (config: BuildConfig) => {
  console.log('üî® ÊûÑÂª∫ CJS Ê†ºÂºè...')

  const componentsRoot = config.componentsRoot || 'src'
  const inputList = [
    `${componentsRoot}/**/*.ts`,
    `${componentsRoot}/**/*.tsx`,
    `!${componentsRoot}/**/demos`,
    `!${componentsRoot}/**/*.d.ts`,
    `!${componentsRoot}/**/type.ts`,
    `!${componentsRoot}/**/types.ts`,
    `!${componentsRoot}/**/__tests__`,
  ]

  // CJS Ê†ºÂºèÊéíÈô§ lodash-es
  const cjsExternalException = ['lodash-es']
  const external = [...(config.external || []), 'vue'].filter((value) =>
    typeof value === 'string' ? !cjsExternalException.includes(value) : true,
  )

  const bundle = await rollup({
    input: inputList,
    external,
    plugins: [multiInput({ relative: componentsRoot }), ...getPlugins({ cssBuildType: 'ignore', config })],
  })

  await bundle.write({
    banner: createBanner(config),
    dir: `${config.outDir || '.'}/cjs/`,
    format: 'cjs',
    sourcemap: true,
    exports: 'named',
    chunkFileNames: '_chunks/dep-[hash].js',
  })

  console.log('‚úÖ CJS Ê†ºÂºèÊûÑÂª∫ÂÆåÊàê')
}

/**
 * ÊûÑÂª∫ UMD Ê†ºÂºè
 */
export const buildUmd = async (config: BuildConfig, isMin = false) => {
  console.log(`üî® ÊûÑÂª∫ UMD Ê†ºÂºè${isMin ? 'ÔºàÂéãÁº©ÁâàÔºâ' : ''}...`)

  const input = config.input || `${config.componentsRoot || 'src'}/index.ts`
  const external = ['vue']
  const globals = { vue: 'Vue', ...config.globals }

  const bundle = await rollup({
    input,
    external,
    plugins: getPlugins({
      cssBuildType: 'single',
      isProd: isMin,
      env: isMin ? 'production' : 'development',
      config
    }),
  })

  await bundle.write({
    name: config.name,
    banner: createBanner(config),
    format: 'umd',
    exports: 'named',
    globals,
    sourcemap: true,
    file: `${config.outDir || '.'}/dist/${config.name}${isMin ? '.min' : ''}.js`,
  })

  console.log(`‚úÖ UMD Ê†ºÂºè${isMin ? 'ÔºàÂéãÁº©ÁâàÔºâ' : ''}ÊûÑÂª∫ÂÆåÊàê`)
}

