/**
 * Âà†Èô§ËæìÂá∫ÁõÆÂΩï
 */
export const deleteOutput = async (config: BuildConfig) => {
  console.log('üóëÔ∏è Ê∏ÖÁêÜËæìÂá∫ÁõÆÂΩï...')

  const dirs = ['es', 'esm', 'lib', 'cjs', 'dist']
  const outDir = config.outDir || '.'

  const removePromises = dirs.map(async (dir) => {
    try {
      await remove(`${outDir}/${dir}`)
    } catch (error) {
      // ÂøΩÁï•Âà†Èô§Â§±Ë¥•ÁöÑÈîôËØØ
    }
  })

  await Promise.all(removePromises)
  console.log('‚úÖ ËæìÂá∫ÁõÆÂΩïÊ∏ÖÁêÜÂÆåÊàê')
}

/**
 * ÊûÑÂª∫ÊâÄÊúâÊ†ºÂºèÔºàTDesign È£éÊ†ºÔºâ
 */
export const buildComponents = async (config: BuildConfig) => {
  console.log('üöÄ ÂºÄÂßã TDesign È£éÊ†ºÁöÑÂ§öÊ†ºÂºèÊûÑÂª∫...')

  // Ê∏ÖÁêÜËæìÂá∫ÁõÆÂΩï
  await deleteOutput(config)

  // ÊåâÈ°∫Â∫èÊûÑÂª∫ÂêÑÁßçÊ†ºÂºè
  await buildEs(config)
  await buildEsm(config)
  await buildLib(config)
  await buildCjs(config)
  await buildUmd(config)
  await buildUmd(config, true)

  // ÊûÑÂª∫È¢ùÂ§ñÁöÑ CSS Êñá‰ª∂
  if (config.isVueLibrary) {
    await buildResetCss(config)
    await buildPluginCss(config)
  }

  console.log('üéâ TDesign È£éÊ†ºÁöÑÂ§öÊ†ºÂºèÊûÑÂª∫ÂÆåÊàêÔºÅ')
}
